.date-wrapper {
    width: 80%;
    margin: 40px auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    @media(max-width: 768px) {
        width: 90%;
    }

    .item {
        width: 120px;
        height: 80px;
        margin: 10px;
        padding: 7px;
        border-radius: 10px;
        background-color: #629786;
        box-shadow: 0 10px 30px -10px hsla(0, 0%, 0%, 0.3);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-family: Arial, Helvetica, sans-serif;
        transform: translateY(40px);
        opacity: 0;

        .mid {
            width: 100%;
            height: 50%;
            margin-top: 17px;
            font-size: 27px;
        }

        .effect {
            width: 100%;
            height: 50%;
            font-size: 13px;
        }

    }
}